import { useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";



const AboutPage : React.FC = () => {

    useEffect(()=>{
        const observer = new IntersectionObserver((entries)=>{
            entries.forEach((entry)=>{
                if(entry.isIntersecting){
                    entry.target.classList.add('show');
                }else{
                    entry.target.classList.remove('show');
                }
            })
        })

        const elements = document.querySelectorAll('.hidden');
        elements.forEach((el)=>observer.observe(el));

    }, []);

    return (
        <div className="aboutpage-wrapper">
            <Container>
                <Row className="align-item-center">
                    {/*   */}
                    <Col md={6} className="header_left hidden">
                        <header className="header_left_intro">  </header>
                        <header className="header_left_introduce">.</header>
                        <header className="header_left_introduce">.</header>                        
                        <div className="header_left_introduce_body_container">
                            <span className="header_left_introduce_body">   ,</span><br />
                            <span className="header_left_introduce_body">    </span><br />
                            <span className="header_left_introduce_body">    .</span>
                        </div>
                        
                        <div>
                            <a href="https://github.com/DailyOfDaily"><button className="btn_header">GitHub</button></a>
                                <p></p>
                            <a href="https://blog.naver.com/jangtyu"><button className="btn_header"></button></a>
                        </div>
                    </Col>
                    {/*   */}
                    <Col>
                        <div>
                        <p><br /></p><img src="/images/panda.png" width={"70%"} /><br /><br />
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default AboutPage;